* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: sans-serif;
}

header {
	height: 58px;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.07), 0 1px 2px 0 rgba(0, 0, 0, 0.05);
	color: #212529;
}

.nav-area {
	display: flex;
	justify-content: center;
	align-items: center;
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 20px;
	height: 58px;
}

.logo {
	text-decoration: none;
	font-size: 25px;
	color: inherit;
	margin-right: 20px;
}

.menus {
	display: flex;
	list-style: none;
}

.menu-items {
	position: relative;
	font-size: 14px;
}

.menu-items a {
	display: block;
	font-size: inherit;
	color: inherit;
	text-decoration: none;
}

.menu-items button {
	color: inherit;
	font-size: inherit;
	border: none;
	background-color: transparent;
	cursor: pointer;
	width: 100%;
}

.menu-items a,
.menu-items button {
	text-align: left;
	padding: 0.7rem 1rem;
}

.menu-items a:hover,
.menu-items button:hover {
	background-color: #f2f2f2;
}

.arrow::after {
	content: "";
	display: inline-block;
	margin-left: 0.28em;
	vertical-align: 0.09em;
	border-top: 0.42em solid;
	border-right: 0.32em solid transparent;
	border-left: 0.32em solid transparent;
}

.dropdown {
	position: absolute;
	right: 0;
	left: auto;
	box-shadow: 0 10px 15px -3px rgba(46, 41, 51, 0.08), 0 4px 6px -2px rgba(71, 63, 79, 0.16);
	font-size: 0.875rem;
	z-index: 9999;
	min-width: 10rem;
	padding: 0.5rem 0;
	list-style: none;
	background-color: #fff;
	border-radius: 0.5rem;
	display: none;
}

.dropdown.show {
	display: block;
}

.dropdown .dropdown-submenu {
	position: absolute;
	left: 100%;
	top: -7px;
}

.modal {
	width: 300px;
	height: 150px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 45%;
	left: 45%;
	padding: 50px;
	border-radius: 20px;
	box-shadow: #595c5f 0px 0px 20px;
	&__input {
		background: none;
		border: none;
	}
	.context-li {
		width: 100%;
	}
	&__context-menu {
		box-shadow: #595c5f 0px 0px 20px;
	}
	.modal-btn {
		button {
			border: none;
			text-align: left;
			padding: 0.7rem 1rem;
			cursor: pointer;
			background: #fff;
			&:hover {
				background-color: #f2f2f2;
			}
		}
	}
}
